body{
    margin : 0;
}
.login{
    background-color: #555;
    min-height: 100vh;
    height: 100%;
    width: 100%;
    
    .login-warpper{
        max-width: 360px;
        margin: 0 auto;
        padding-top: 10vh;
        padding-bottom: 25px;

        .login-form {
            padding: 20px 30px;
            background: #444444;
            border-radius: 5px;
        }
        .login-logo{        
            text-align: center;
            svg {
                height: 40px;
                margin-bottom: 5px;
            }
        }
        .login-form-button{
            width:100px;
        }
    }
    .version-log{
        margin-top: 10px;
        text-align: center;
    }
    .text-center {
        text-align: center;
    }
    .gray-fonts{
        color: #ddd;
    }
}